import React, { useEffect, useState } from 'react';
import PerfectScrollbar from 'react-perfect-scrollbar';
import * as yup from 'yup';
import { useDispatch } from 'react-redux';
import { yupResolver } from '@hookform/resolvers/yup';
import { useForm } from 'react-hook-form';
import { Button, Dialog, DialogActions, DialogContent, DialogTitle, Typography, Grid, Link, Divider } from '@material-ui/core';
import { getAllParentCategory } from '../../../../../store/actions/BookCategoryActions';
import { FormProvider, RHFTextField, RHFSelect } from '../../../../../components/hook-form';
import CloseIcon from '@material-ui/icons/Close';
import useStyles from '../../../../DialogStyles';
import { IconButton } from '@mui/material';
import { addParentCategory, updateParentCategory } from '../../../../../services/BookParentServices';
import { setMessage } from '../../../../../store/actions/MessageActions';

const STATUS_ITEMS = [
    { id: 0, status: 'ACTIVE' },
    { id: 1, status: 'INACTIVE' }
];

const validationSchema = yup.object({
    title: yup.string('Enter Title').required('Title is required').trim(),
    description: yup.string().optional().trim(),
    status: yup.string('Choose Status').required('Status is required')
});

export default function AddParentCategory(props) {
    const [file, setFile] = useState(null);
    const { isOpen, setOpen, isEditData } = props;
    const [displayImage, setDisplayImage] = useState();
    const classes = useStyles();
    const dispatch = useDispatch();

    const initialValues = {
        title: isEditData?.title || '',
        description: isEditData?.description || '',
        status: isEditData?.status || 'ACTIVE'
    };
    const methods = useForm({
        mode: 'onChange',
        resolver: yupResolver(validationSchema),
        defaultValues: initialValues
    });

    const { reset, handleSubmit } = methods;

    const handlePopupClose = () => {
        setOpen(false);
        reset();
    };
    const handleChange = function loadFile(event) {
        setDisplayImage(URL.createObjectURL(event.target.files[0]));
        setFile(event?.target?.files[0]);
    };

    const onSubmit = (data) => {
        const payload = {
            title: data?.title,
            description: data?.description,
            status: data?.status
        };

        const formData = new FormData();
        if (file) formData.append('image', file);
        if (isEditData) formData.append('parentCategoryBody ', JSON.stringify(payload));
        else formData.append('parentCategoryBody ', JSON.stringify(payload));

        if (isEditData) {
            updateParentCategory(formData, isEditData.id)
                .then((data) => {
                    dispatch(getAllParentCategory());
                    dispatch(setMessage(data?.message ? data?.message : 'Book Parent Category updated Successfully'));
                })
                .catch((error) => {
                    dispatch(setMessage(error?.data?.message ? error?.data?.message : 'Something Went Wrong'));
                });
            handlePopupClose();
        } else {
            addParentCategory(formData)
                .then((data) => {
                    dispatch(getAllParentCategory());
                    dispatch(setMessage(data?.message ? data?.message : 'Added Successfully!'));
                })
                .catch((error) => {
                    console.log(error);
                    dispatch(setMessage(error?.data?.message ? error?.data?.message : 'Something went wrong!'));
                });
            handlePopupClose();
        }
    };
    useEffect(() => {
        dispatch(getAllParentCategory());
    }, []);

    // useEffect(() => {
    //     if (isEditData) {
    //         setDisplayImage(isEditData?.imageUrl);

    //         fetch(isEditData?.imageUrl, { mode: 'no-cors' }).then(async (response) => {
    //             const contentType = response.headers.get('content-type');
    //             const blob = await response.blob();
    //             const file = new File([blob], 'updatedImage.jpg', { contentType });
    //             setFile(file);
    //         });
    //     }
    // }, []);

    useEffect(() => {
        if (isEditData) {
            setDisplayImage(isEditData.imageUrl);

            fetch(isEditData?.imageUrl, { mode: 'no-cors' }).then(async (response) => {
                const contentType = response.headers.get('content-type');
                const blob = await response.blob();
                const file = new File([blob], 'updatedImage.jpg', { contentType });
                // setFile(file);
            });
        }
    }, []);

    return (
        <div>
            <Dialog open={isOpen} classes={{ paper: classes.dialog }}>
                <DialogTitle className={classes.dialogTitle}>
                    <div className={classes.dialogTitleChild}>
                        <Typography style={{ fontSize: '20px' }}>
                            {isEditData ? 'Update Book Parent Category' : 'Add Book Parent Category'}
                        </Typography>
                        <Link to="#" onClick={handlePopupClose}>
                            <CloseIcon className={classes.closeIcon} />
                        </Link>
                    </div>
                </DialogTitle>
                <FormProvider methods={methods} onSubmit={handleSubmit(onSubmit)}>
                    <PerfectScrollbar className={classes.ScrollHeight}>
                        <DialogContent>
                            <div>
                                <label className={classes.label}>
                                    Book Image <span className={classes.mandatory}>*</span>
                                </label>
                                <div>
                                    {!isEditData ? (
                                        <input
                                            type="file"
                                            onChange={handleChange}
                                            id="upload"
                                            name="image"
                                            accept="image/*"
                                            className={classes.imageInput}
                                            required
                                        />
                                    ) : (
                                        <input
                                            type="file"
                                            onChange={handleChange}
                                            id="upload"
                                            name="image"
                                            accept="image/*"
                                            className={classes.imageInput}
                                        />
                                    )}
                                    
                                    <label htmlFor="upload">
                                        <IconButton color="secondary" aria-label="upload picture" component="span">
                                            <img
                                                id="ImageCover"
                                                src={displayImage}
                                                className={classes.img}
                                                style={{ height: '200px', width: '200px' }}
                                            />
                                        </IconButton>
                                    </label>
                                    <label htmlFor="bookCover" />
                                </div>
                            </div>
                            
                            <Grid container>
                                <Grid item xs={12}>
                                    <label>
                                        Category Title <span className={classes.mandatory}>*</span>
                                    </label>
                                    <RHFTextField name="title" placeholder="Title" />
                                    <br /> <br />
                                    <label> Category Description </label>
                                    <RHFTextField name="description" placeholder="Description" />
                                    <br /> <br />
                                    <label>
                                        Status <span className={classes.mandatory}>*</span>
                                    </label>
                                    <RHFSelect name="status" label="">
                                        {STATUS_ITEMS?.map((item, index) => (
                                            <option key={index} value={item?.status}>
                                                {item?.status}
                                            </option>
                                        ))}
                                    </RHFSelect>
                                </Grid>
                            </Grid>
                        </DialogContent>
                    </PerfectScrollbar>
                    <Divider style={{ marginTop: 15 }} />
                    <DialogActions className={classes.dialogAction}>
                        <Button variant="outlined" color="secondary" className={classes.buttonCancel} onClick={handlePopupClose}>
                            Close
                        </Button>
                        <Button variant="contained" color="secondary" type="submit" className={classes.buttonSubmit}>
                            {isEditData ? 'Update' : 'Add'}
                        </Button>
                    </DialogActions>
                </FormProvider>
            </Dialog>
        </div>
    );
}
