import { http } from '../utils/http';

export const addParentCategory = (payload) => {
    return new Promise((resolve, reject) => {
        http(
            process.env.REACT_APP_ROOT_URL_BOOK,
            'POST',
            'books-service/parent-categories',
            payload,
            true,
            { 'content-type': 'multipart/form-data', accept: '*/*' },
            {}
        )
            .then((data) => {
                resolve(data);
            })
            .catch((err) => {
                reject(err);
            });
    });
};

export const updateParentCategory = (payload, categoryId) => {
    return new Promise((resolve, reject) => {
        http(
            process.env.REACT_APP_ROOT_URL_BOOK,
            'PUT',
            `books-service/parent-categories/${categoryId}`,
            payload,
            true,
            { 'content-type': 'multipart/form-data', accept: '*/*' },
            {}
        )
            .then((data) => {
                resolve(data);
            })
            .catch((err) => {
                reject(err);
            });
    });
};
