import React, { useState, useEffect } from 'react';
import MainCard from '../../../../../ui-component/cards/MainCard';
import { withStyles, useTheme } from '@material-ui/styles';
import {
    Grid,
    IconButton,
    Typography,
    Button,
    Table,
    TableBody,
    TableCell,
    TableContainer,
    TableHead,
    TableRow,
    Paper,
    TablePagination,
    OutlinedInput,
    InputAdornment,
    Tooltip
} from '@material-ui/core';
import AddIcon from '@material-ui/icons/Add';
import { IconSearch } from '@tabler/icons';
import EditIcon from '@material-ui/icons/Edit';
import SubjectIcon from '@material-ui/icons/Subject';
import AddParentCategory from '../add-parent-category';
import { useDispatch, useSelector } from 'react-redux';
import { getAllParentCategory } from '../../../../../store/actions/BookCategoryActions';
import useStyles from '../../styles';
import useUserRole from '../../../../../hooks/useUserRole';

const StyledTableRow = withStyles((theme) => ({
    root: {
        '&:nth-of-type(even)': {
            backgroundColor: theme.palette.action.hover
        }
    }
}))(TableRow);

export default function ViewParentCategories() {
    const classes = useStyles();
    useUserRole({ roleList: ['ADMIN', 'SUPER_ADMIN'] });
    const dispatch = useDispatch();
    const theme = useTheme();
    const [page, setPage] = useState(0);
    const [rowsPerPage, setRowsPerPage] = useState(25);
    const [data, setData] = useState([]);
    const [isSearch, setSearch] = useState('');
    const [openAddParentCategory, setOpenAddParentCategory] = useState(false);
    const [isParentCategoryUpdateData, setParentCategoryUpdateData] = useState(null);
    const { allParentCategories } = useSelector((state) => state?.category);

    const requestSearch = (searchedVal) => {
        const filteredRows = data.filter((row) => {
            return Object.keys(row).some((key) => row[key].toLowerCase().includes(searchedVal));
        });
        setData(filteredRows);
    };

    const handleChangePage = (event, newPage) => {
        setPage(newPage);
    };

    const handleChangeRowsPerPage = (event) => {
        setRowsPerPage(+event.target.value);
        //setPage(0);
    };

    const handleAddParentCategory = (id) => {
        setOpenAddParentCategory(true);
        setParentCategoryUpdateData(id);
    };

    const handleSearch = (event) => {
        setSearch(event.target.value);
    };

    useEffect(() => {
        dispatch(getAllParentCategory(rowsPerPage, isSearch ? isSearch : '', page + 1));
    }, [rowsPerPage, isSearch, page]);

    return (
        <>
            <MainCard>
                <div className={classes.titleContainer}>
                    <SubjectIcon className={classes.viewIcon} />
                    <span className={classes.pageTitle}>&nbsp; All Parents Categories </span>
                </div>
                <Paper elevation={5} className={classes.root}>
                    <Grid container spacing={3} style={{ display: 'flex', justifyContent: 'space-between' }}>
                        <Grid item xs={12} sm={6} md={6}>
                            <div>
                                <OutlinedInput
                                    className={classes.searchControl}
                                    id="input-search-header"
                                    onChange={(event) => handleSearch(event)}
                                    placeholder="Search"
                                    startAdornment={
                                        <InputAdornment position="start">
                                            <IconSearch stroke={1.5} size="1rem" className={classes.startAdornment} />
                                        </InputAdornment>
                                    }
                                />
                            </div>
                        </Grid>
                        <Grid item xs={12} sm={6} md={6}>
                            <div className={classes.btn}>
                                <Button
                                    variant="contained"
                                    color="primary"
                                    className={classes.btnAddParent}
                                    onClick={() => {
                                        setOpenAddParentCategory(true);
                                        setParentCategoryUpdateData(null);
                                    }}
                                >
                                    <AddIcon /> New
                                </Button>
                            </div>
                        </Grid>
                    </Grid>
                    <TableContainer className={classes.tableContainer}>
                        <Table className={classes.table} aria-label="simple table">
                            <TableHead className={classes.tableHeaderCell}>
                                <StyledTableRow>
                                    <TableCell> Title</TableCell>
                                    <TableCell align="center">Status</TableCell>
                                    <TableCell align="right"> Action </TableCell>
                                </StyledTableRow>
                            </TableHead>
                            {!allParentCategories ? (
                                <caption style={{ textAlign: 'center' }}>Sorry! No data available</caption>
                            ) : (
                                <TableBody>
                                    {allParentCategories?.content?.map((row) => (
                                        <StyledTableRow key={row?.id}>
                                            <TableCell>{row?.title}</TableCell>

                                            <TableCell align="center">
                                                <Typography
                                                    className={classes.status}
                                                    style={{
                                                        backgroundColor:
                                                            (row?.status === 'ACTIVE' && theme.palette.success.main) ||
                                                            (row?.status === 'PENDING' && 'blue') ||
                                                            (row?.status === 'INACTIVE' && theme.palette.warning.dark)
                                                    }}
                                                >
                                                    {row?.status}
                                                </Typography>
                                            </TableCell>
                                            <TableCell align="right">
                                                <Tooltip title="Update Parent Category">
                                                    <IconButton onClick={() => handleAddParentCategory(row)}>
                                                        <EditIcon color="primary" style={{ cursor: 'pointer' }} />
                                                    </IconButton>
                                                </Tooltip>
                                            </TableCell>
                                        </StyledTableRow>
                                    ))}
                                </TableBody>
                            )}
                        </Table>
                    </TableContainer>
                    <TablePagination
                        rowsPerPageOptions={[25, 50, 100]}
                        component="div"
                        count={allParentCategories?.totalElements || 0}
                        rowsPerPage={rowsPerPage}
                        page={page}
                        onPageChange={handleChangePage}
                        onRowsPerPageChange={handleChangeRowsPerPage}
                        sx={{
                            '.MuiTablePagination-displayedRows': {
                                marginBottom: 0
                            },
                            '.MuiTablePagination-selectLabel': {
                                marginBottom: 0
                            }
                        }}
                    />
                </Paper>
                {openAddParentCategory && (
                    <AddParentCategory
                        isOpen={openAddParentCategory}
                        setOpen={setOpenAddParentCategory}
                        isEditData={isParentCategoryUpdateData}
                    />
                )}
            </MainCard>
        </>
    );
}
